import React, { Component } from "react";
import Educations from "../../containers/education/Educations";
import CompetitiveSites from '../../components/competitiveSites/CompetitiveSites';
import {competitiveSites}  from "../../portfolio";
import "./EducationComponent.css";
import { Fade } from "react-reveal";
import { talkSection } from "../../portfolio";
import emoji from "react-easy-emoji";

class Education extends Component {

  render() {
    return (
      <div className="education-main">
        <div className="basic-education">
          <Fade bottom duration={1000} distance="40px">
            <div className="heading-div">
    <div className="main" id="talks">
      <div className="talk-header">
        <h1 className="talk-header-title">{talkSection.title}</h1>
        <p className="subTitle talk-header-subtitle">{talkSection.subtitle}</p>
      </div>
    </div>
</div>
	</Fade>

          <Educations/>
        <Fade bottom duration={1000} distance="20px">
        <div className="main contact-margin-top" id="contact">
            <div className="contact-div-main">
                <div className="contact-header">
                    <h1 className="heading contact-title">Here I Code :)</h1>
                    <p className="subTitle contact-subtitle"><CompetitiveSites logos={competitiveSites.competitiveSites} /></p>
		</div>
	    </div>
	</div>
          </Fade>

        </div>
      </div>
    );
  }
}

export default Education;